










































	export default{
		data(){
			return{
				data:'',
				switchBlean:true,
				checked2:true,
				checked:false,
				appdata:''
			}
		},
		methods:{
			switch1Change: function (e) {
				// console.log('switch1 发生 change 事件，携带值为', e.target.value)
				this.checked=!this.checked;
				uni.setStorageSync('swich2',this.checked);
				if(e.target.value){
					uni.showToast({
						title:'开启了推送',
						icon:'none'
					})
				}else{
					uni.showToast({
						title:'关闭了推送',
						icon:'none'
					})
				}
			},
			switch2Change: function (e) {
				// console.log('switch2 发生 change 事件，携带值为', e.target.value)
				this.checked2=e.target.value;
				uni.setStorageSync('switch2',e.target.value);
				if(e.target.value){
					uni.showToast({
						title:'开启了弹窗',
						icon:'none'
					})
				}else{
					uni.showToast({
						title:'关闭了弹窗',
						icon:'none'
					})
				}
			},
			clearData(){
             var that=this;
             plus.cache.calculate( function ( size ) {
				 if(size<1024){
					 that.appdata=size+'b';
				 }  
				 else if(size/1024>=1 && size/1024/1024<1){
					  that.appdata= Math.floor(size/1024*100)/100+'kB';
				 }
				else if(size/1024/1024>=1){
		             that.appdata=Math.floor(size/1024/1024*100)/100+'M';
				}
             	  uni.showModal({
             	  	title:'提示',
             	  	content:'确定清除缓存吗?',
             	  	success(res) {
						if(res.confirm){
							plus.cache.clear( function () {
								uni.showToast({
									title:'清除成功',
									icon:'none',
									success() {
										that.appdata=0+'b';
									}
								})
							});	
						}
             	  	}
             	  })
             });
			},
			singout(){
				uni.showModal({
					title:'提示',
					showCancel:true,
					content:'你确定要退出当前账号吗',
					success(res) {
						if(res.confirm){
							// console.log('点击了确定');
							uni.clearStorage();
							uni.switchTab({
								url:'../index/index'
							})
						}
					}
				})
			},
			appdatas(){
				var that=this;
				plus.cache.calculate( function ( size ) {
								 if(size<1024){
									 that.appdata=size+'b';
								 }  
								 else if(size/1024>=1 && size/1024/1024<1){
									  that.appdata= Math.floor(size/1024*100)/100+'kB';
								 }
								 else if(size/1024/1024>=1){
									 that.appdata=Math.floor(size/1024/1024*100)/100+'M';
								 }
							});		 
			},
			hleps(){
				uni.navigateTo({
					url:'../help/help'
				})
			},
			shareApp(){
				uni.navigateTo({
					url:'../shareapp/shareapp'
				})
			},
			amend(){
				uni.navigateTo({
					url: '../bind/bind?first=true&new=true'
				});
			}
		},
		onLoad() {
			uni.hideShareMenu();
			this.checked2=uni.getStorageSync('switch2');
			this.checked=uni.getStorageSync('swich2');



			this.checked2=uni.getStorageSync('switch2')===''?true:uni.getStorageSync('switch2')
		},
		
	}
